@import "Variables.css";

.modal{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}

.modal-active{
    pointer-events: all;
    opacity: 1;
    transform: scale(1);
}

.modal_content{
    background-color: white;
    border-radius: 10px;
    padding: 12px;
    transform: scale(0.5);
    transition: 0.4s all
}

.modal_content-active{
    transform: scale(1);
}