@import "./Variables.css";

.chat{
    width: var(--chat-width);
    height: var(--chat-height);
    border: 2px solid black;
    background-color: var(--background-color);
    position: absolute;
    z-index: 100;
    /*bottom: 0;*/
    /*right: 0;*/
    margin-left: 200px;
    overflow-x: scroll;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
}

.msg_container{
    height: 90%;
    width: 100%;
    max-height: var(--chat-height);
    overflow-x: scroll;
    padding: 5px;
}

.send_container{
    height: 10%;
    margin-top: auto;
    width: 100%;
    display: flex;
    justify-content: space-between;
    background-color: white;
    align-items: center;
    padding: 5px;
    border-top: 1px solid grey;
}

.send_button{
    height: 65%;
    width: 25%;
    border-radius: 10px;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    border: 2px solid black;
}

.msg_input{
    height: 65%;
    font-size: 18px;
    width: 70%;
    border-radius: 10px;
    padding-left: 5px;
    outline: none;
    border: 2px solid black;
}

.msg{
    margin-top: 5px;
    max-width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
    border: 1px solid crimson;
    border-radius: 5px;
    padding: 2px;
}